export default {
	props: ['dataList', 'state'],
	components: {
		haoheaoScroll
	},
	data() {
		return {

		}
	},
	computed: {

	},
	methods: {
		onPullDown(done) { // 下拉刷新
			this.$emit('again');
			// this.$emit('againMore', this.state)
			setTimeout(() => {
				done();
			}, 1000)
		},
		onLoadMore(e) { // 加载更多
